import { useTranslation } from "react-i18next";
import style from "../../../FinanceManager/Income/Income.module.css";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import TileWrapper from "../../../../Ui/Tab/Tile/TileWrapper";
import TileCard from "../../../../Ui/Tab/Tile/TileCard";
import FeeStatisticsDepartmentFilter from "../../../FinanceManager/FeeStatistics/FeeStatisticsDepartmentFilter";
import FeeStatisticsBatchFilter from "../../../FinanceManager/FeeStatistics/FeeStatisticsBatchFilter";
import FeeStatisticsStandardFilter from "../../../FinanceManager/FeeStatistics/FeeStatisticsStandardFilter";
import {
  batchLabel,
  departmentLabel,
} from "../../../../Constant/Statistics/statistics_constant";
import { useCallback } from "react";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import CreateButton from "../../../../Ui/Button/CreateButton";
const PendingFeesSendReminderFilter = ({
  applyFilter,
  setApplyFilter,
  onClose,
  fid,
  onActionEventFire,
  instituteId,
}) => {
  const { t } = useTranslation();
  const onDepartmentTile = (val) => {
    setApplyFilter((prev) => ({
      ...prev,
      all_depart: val,
      depart: "",
      batch_status: val,
      batch: "",
      master: "",
    }));
  };
  const onBatchTile = (val) => {
    setApplyFilter((prev) => ({ ...prev, batch_status: val, batch: "" }));
  };
  const onSelectBatch = useCallback((value) => {
    setApplyFilter((prev) => ({
      ...prev,
      batch_status: "PARTICULAR_BATCH",
      batch: value,
    }));
  }, []);
  const onSelectStandard = useCallback((value) => {
    setApplyFilter((prev) => ({ ...prev, master: value }));
  }, []);

  const onSelectDepartment = useCallback((value) => {
    setApplyFilter((prev) => ({
      ...prev,
      all_depart: "PARTICULAR",
      depart: value,
    }));
  }, []);

  return (
    <Modal>
      <div className={style.modal_container}>
        <UserMemberContentWrapper>
          <div className={style.modal_container_outer_header}>
            <div className={style.modal_container_header}>
              <h6>{t("apply_filter")}</h6>
            </div>
            <img
              src={`${assestsUserFinanceUrl}/close.svg`}
              alt="close icon"
              onClick={onClose}
            />
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0",
          }}
        >
          <TileWrapper label="department_type">
            {departmentLabel?.map((tile) => (
              <TileCard
                key={tile?.key}
                tile={tile}
                onTile={onDepartmentTile}
                activeTile={applyFilter?.all_depart}
              />
            ))}
          </TileWrapper>
          {applyFilter?.all_depart === "PARTICULAR" ? (
            <>
              <FeeStatisticsDepartmentFilter
                fid={fid}
                onSelect={onSelectDepartment}
                onlyDepartment
                defalutValue={applyFilter?.depart?.dName}
              />
              <TileWrapper label="batch_type">
                {batchLabel?.map((tile) => (
                  <TileCard
                    key={tile?.key}
                    tile={tile}
                    onTile={onBatchTile}
                    activeTile={applyFilter?.batch_status}
                  />
                ))}
              </TileWrapper>

              {applyFilter?.batch_status === "PARTICULAR_BATCH" ? (
                <FeeStatisticsBatchFilter
                  did={applyFilter?.depart?._id}
                  onSelect={onSelectBatch}
                  onlyBatch
                  defalutValue={applyFilter?.batch?.batchName}
                />
              ) : null}
              <FeeStatisticsStandardFilter
                did={applyFilter?.depart?._id}
                onSelect={onSelectStandard}
                instituteId={instituteId}
                defalutValue={applyFilter?.master?.className}
              />
            </>
          ) : null}

          <CreateButton label="next" onAction={onActionEventFire} />
        </UserMemberContentWrapper>
      </div>
    </Modal>
  );
};

export default PendingFeesSendReminderFilter;
